import React from 'react';
import './header.css';
import Unit from '../unit';
import Animaballs from '../animaballs';
import { Accordion, Card, Button } from 'react-bootstrap';

const Header = () => {
    return (
        <div className="header">
            <Accordion defaultActiveKey="1">
                <Card>
                    <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            <Animaballs />
                            
                        </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body>
                            <div className="nav-top">
                                <Unit
                                    title=""
                                    topics={[
                                        { href: '/oop', text: '' },
                                        { href: '/func', text: ' ' },
                                    ]}
                                />
                                <Unit
                                    title=""
                                    topics={[
                                        { href: '/solid', text: 'SOLID' },
                                        { href: '/kiss', text: 'KISS' },
                                        { href: '/yagni', text: 'YAGNI' },
                                        { href: '/dry', text: 'DRY' },
                                    ]}
                                />
                                <Unit
                                    title=".  . ."
                                    topics={[
                                        { href: '/datastructures', text: ' ' },
                                        { href: '/datastructurejs', text: '    JS' },
                                    ]}
                                />
                                <Unit
                                    title="JavaScript"
                                    topics={[
                                        { href: '/jsinheritance', text: '  js' },
                                        { href: '/jscontext', text: '' },
                                        { href: '/comments', text: '' },
                                        { href: '/ustrict', text: ' ' },
                                        { href: '/debandthrott', text: 'Throttling  Debouncing' },
                                    ]}
                                />
                                <Unit
                                    title=""
                                    topics={[
                                        { href: '/loadingpage', text: ' ' },
                                        { href: '/req', text: '' },
                                        { href: '/rest', text: ' REST' },
                                        { href: '/react-redux', text: 'React-redux' },
                                    ]}
                                />
                                <Unit
                                    title=""
                                    topics={[
                                        { href: '/bem', text: ' ' },
                                        { href: '/adaptive', text: '/' },
                                    ]}
                                />
                                <Unit
                                    title=""
                                    topics={[
                                        { href: '/jsnotes', text: '  js' },
                                        { href: '/reactnotes', text: 'C  ' },
                                        { href: '/other', text: ' ' },
                                    ]}
                                />
                                <Unit
                                    title="  "
                                    topics={[{ href: '/videobasejs', text: '  js' }]}
                                />
                            </div>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
            </Accordion>
        </div>
    );
};

export default Header;
